/* ==========================================================================
   WooCommerce Buttons responsive styles - begin
   ========================================================================== */
@include laptop-landscape-large {
    .edgtf-woo-pl-info-below-image {
        ul.products>.product {
            .added_to_cart,
            .button {
                @include edgtfButtonSize(medium);
            }
        }
    }
}

@include ipad-landscape {

    .woocommerce-page .edgtf-content,
    div.woocommerce {
    
        a.button,
        a.added_to_cart,
        input[type="submit"],
        button[type="submit"]:not(.edgtf-woo-search-widget-button):not(.edgtf-search-submit),
        .wc-forward:not(.added_to_cart):not(.checkout-button) {
            @include edgtfButtonSize(medium);
        }
    }
}
/* ==========================================================================
   WooCommerce Buttons responsive styles - end
   ========================================================================== */