/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - begin
   ========================================================================== */

@media only screen and (min-width: $ipad-landscape-plus-pixel) and (max-width: $laptop-landscape) {
    
    .edgtf-woo-single-page {
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    
                    a {
                        padding: 13px 10px;
                    }
                }
            }
        }
    }
}

@include ipad-landscape {
    
    .edgtf-woo-single-page {
        
        .edgtf-single-product-content {
            
            .images,
            .edgtf-single-product-summary {
                float: none;
                width: 100%;
                padding: 0;
            }
	        
	        .images {
		
		        .woocommerce-product-gallery__trigger {
			        right: 10px;
		        }
	        }
            
            .edgtf-single-product-summary {
                margin: 30px 0 0;
            }
        }
        
        .woocommerce-tabs {
            
            ul.tabs {
                
                > li {
                    &:after {
                        border-bottom: 53px solid transparent;
                    }
                }
            }
        }
    }
}

@include phone-landscape {
    
    .edgtf-woo-single-page {
	
	    &.edgtf-woo-single-thumb-on-left-side {
		
		    .edgtf-single-product-content {

                .woocommerce-tabs {

                    ul.tabs {
                        > li {
                            a {
                                padding: 13px 25px
                            }
                        }
                    }

                    .entry-content.woocommerce-Tabs-panel--description{
                        padding: 40px 20px 54px;
                    }
                }

			    .images {
				
				    &.woocommerce-product-gallery--columns-4 {
					
					    .woocommerce-product-gallery__image {
						
						    &:not(:first-child) {
							    width: 50%;
						    }
					    }
				    }
			    }
		    }
	    }
        
        .edgtf-single-product-content {
            
            .images {
    
                &.woocommerce-product-gallery--columns-4 {
        
                    .woocommerce-product-gallery__image {
            
                        &:not(:first-child) {
	                        width: 50%;
                        }
                    }
                }
            }
        }

        .woocommerce-tabs #reviews ol.commentlist {
            .comment_container img {
                float: none;
                width: 100px;
            }
            .comment-text {
                padding: 0;
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Single Product Page Responsive styles - end
   ========================================================================== */